/*	=============================================================================
	#embed
	
	アスペクト比を固定させるための記述です。
	16:9、4:3の二種類用意しています。
	youtubeなどの比率を固定させるときに使用してください。
	
	<div class="c-embed">
		<video class="c-embed__16to9" src="sample.mp4"></video>
	</div>
	
	========================================================================== */

.c-embed {
  display: block;
  overflow: hidden;
  position: relative;
  height: 0;
  margin: 0;
  padding: 0;
  .c-embed__item,
  iframe,
  embed,
  object,
  video {
    position: absolute;
    top: 0;
    bottom: 0;
    left: 0;
    width: 100% !important;
    height: 100% !important;
    border: 0;
  }
}

//アスペクト比 16:9
.c-embed__16to9 {
  padding-bottom: percentage(9 / 16) !important;
}

//アスペクト比 4:3
.c-embed__4to3 {
  padding-bottom: percentage(3 / 4) !important;
}