/*	=============================================================================
	#humberger
	
	ハンバーガーメニューのスタイルです。
	レイアウト（位置調整）などは親要素の方にて指定をお願い致します。
	色の変更は変数ファイルの方で対応をお願い致します。
	javascriptにて実装してるので動作を指定しているため、クラス名の変更は禁止とします。
	
	<div class="c-humberger">
		<span></span>
		<span></span>
		<span></span>
	</div>
	
	========================================================================== */

.c-humberger{
	width: 30px;
	height: 24px;
	display: block;
	position: relative;
	margin: 0 auto 2px;
	span{
		width: 100%;
		height: 3px;
		background: $_color-main__text;
		display: block;
		position: absolute;
		left: 0;
		transition: all .2s;
		backface-visibility: hidden;
		&:first-child{
			top: 0px;
		}
		
		&:nth-child(2){
			top: calc( 50% - 1px);
		}
		
		&:nth-child(3){
			bottom: 0px;
		}
	}
	
	&.js-active{
		span{
			&:first-child{
				top:50%;
				transform: translateY(-50%) rotate(-45deg);
			}
			
			&:nth-child(2){
				opacity: 0;
			}
			
			&:nth-child(3){
				top:50%;
				bottom: auto;
				transform: translateY(-50%) rotate(45deg);
			}
		}
	}
}