/*	=============================================================================
	#breadcrumb
	
	パンくずリストです。
	
	========================================================================== */
.c-breadcrumb{
	border-top: 1px solid lighten( $_color-main__text,50%);
	border-bottom: 1px solid lighten( $_color-main__text,50%);
	color: $_color-main__text;
	background: $_color-main;
	padding: 8px 30px;
	font-size: 0;
	text-align: right;
	font-family: $_font-ja_serif;
	@include mq-print(){
		display: none;
	}
	&__inner{
		width: 100%;
		height: auto;
		margin: 0;
	}
	
	li{
		@include font-size(1.2rem);
		display: inline-block;
		color: darken($_color-white,80%);
		margin: 0;
		a{
			color: darken($_color-white,80%);
			text-decoration: none;
		}
		&:after{
			content: '>';
			display: inline-block;
			padding: 0 6px;
		}
		&:last-child:after{
			content: '';
			display: none;
		}
	}
}