/*	=============================================================================
	#image
	
	画像追加する時のスタイルになります。
	キャプションの背景色・文字色は変数ファイルで設定をしてください。
	スタイルの調整は直接追加をお願い致します。
	
	EX)汎用的な入れ方
	<figure class="c-image">
		<img src="sample.jpg" alt="sample" class="c-image__item">
		<figcaption class="c-image__caption">sample</figcaption>
	</figure>
	
	EX)親要素に対して画像サイズが小さい時に中心にスタイリングする時の記述
	<figure class="c-image">
		<img src="sample.jpg" alt="sample" class="c-image__item c-image__item_center">
	</figure>
	
	========================================================================== */
.c-image{
	position: relative;
	display: block;
	width: 100%;
	margin: 0;
	padding: 0;
	text-align: center;
	
	&__item{
		display: block;
		position: relative;
		z-index: 1;
		max-width: 100%;
		height: auto;
		margin: 0;
		
		&_center{
			margin: 0 auto;
		}
		
		@include mq-print(){
			width: auto;
			max-height: 140mm;
			display: block;
			margin: 0 auto;
		}
	}

	&__caption{
		position: absolute;
		display: block;
		bottom:0;
		right: 0;
		z-index: 2;
		padding: 0.4em 0.6em;
		text-shadow: 0 0 3px rgba(#000,0.6) , 0 0 3px rgba(#000,0.6) , 0 0 3px rgba(#000,0.6);
		color: $_color-figure__text;
		font-family: $_font-ja_serif;
		@include font-rem(10px,15px,0);
		@include mq-min(md){
			@include font-rem(11px,15px,0);
		}
	}
}