/*	=============================================================================
	#display
	
	ブラウザサイズによる表示・非表示の設定です。
	ベースのブレイクポイント設定と同一のもので指定をしてください。
	モバイルファーストにて設計しているので、標準値はxsです。
	
	EX)タブレット・PCでブロック要素として表示。スマホで非表示の場合
	<p class="c-dn c-db-md"></p>
	
	EX)タブレット・PCで非表示。スマホでインライン要素として表示
	<br class="c-di c-dn-md">
	
	========================================================================== */

.c-dn{display: none !important;}
.c-di{display: inline !important;}
.c-dib{display: inline-block !important;}
.c-db{display: block !important;}

@each $display in map-keys($breakpoints-min){
	@include mq-min($display){
		.c-dn-#{$display}{display: none !important;}
		.c-di-#{$display}{display: inline !important;}
		.c-dib-#{$display}{display: inline-block !important;}
		.c-db-#{$display}{display: block !important;}
	}
}