// ==========================================================================
// base
// ==========================================================================

html {
	box-sizing: border-box;
	-webkit-text-size-adjust: 100%;
}

*,
*:before,
*:after {
	box-sizing: border-box;
}

:root {
  font-size: $_root-font-size-percent;
}
a {
	color: $_color-link;
	text-decoration: none;

	&:hover {
		color: $_color-link--hover;
	}
}

body {
	-webkit-font-smoothing: antialiased;
	-moz-osx-font-smoothing: grayscale;
	font-feature-settings: "palt";
	margin: 0;
}

article {
	overflow: hidden;
}

img {
	max-width: 100%;
	height: auto;
	vertical-align: middle;
}

ul{
	padding-left: 0;
}

li{
	list-style: none;
}
p{
	margin-bottom: 0;
}

a{
	color: $_color-black;
	&:hover{
		text-decoration: none;
	}
}

//IEでSVGがフルードイメージにならない不具合があるため、属性値が`.svg`で終わる要素に適応。

[src$=".svg"],
[data$=".svg"] {
	width: 100%;
}

//斜体のリセット
i,
cite,
em,
address,
dfn {
	font-style: normal;
}

//計測タグの余計な高さが出る場合の非表示設定
#tag {
	display: none !important;
}