/*	=============================================================================
	#pagetop
	
	ページトップのスタイルです。
	デバイスフォントで記入する形にしていますので、アイコンにする場合などは
	適宜修正をお願い致します。
	javascriptにて実装してるので動作を指定しているため、クラス名の変更は禁止とします。
	
	<p class="c-pagetop"><a href="#wrap">↑</a></p>
	
	========================================================================== */

.c-pagetop {
	position: fixed;
	bottom: 20px;
	right: 20px;
	z-index: 9999;
	@include mq-print(){
		display: none;
	}
	a {
		background: rgba($_color-black,0.8);
		text-decoration: none;
		color: $_color-white;
		padding: 15px;
		border-radius: 50%;
		font-size: 18px;
		line-height: 1em;
		width: calc(18px + 30px);
		text-align: center;
		display: block;
		position: relative;
	}
}
