/*	=============================================================================
	メディアクエリ設定
	========================================================================== */

// -----------------------------------------------------------------
// media query
// 引数$breakpointに渡された値をブレイクポイントにしたmedia query設定を出力する（モバイルファースト）
// -----------------------------------------------------------------
@mixin mq-min($breakpoint: md) {
	@media #{map-get($breakpoints-min, $breakpoint)} {
		@content;
	}
}

// -----------------------------------------------------------------
// media query（pc first）
// 引数$breakpointに渡された値をブレイクポイントにしたmedia query設定を出力する（PCファースト）
// -----------------------------------------------------------------
@mixin mq-max($breakpoint: md) {
	@media #{map-get($breakpoints-max, $breakpoint)} {
		@content;
	}
}

// -----------------------------------------------------------------
// media query（for print）
// 印刷用
// -----------------------------------------------------------------
@mixin mq-print() {
	@media print {
		@content;
	}
}

// -----------------------------------------------------------------
// media query（for IE11）
// IE用
// -----------------------------------------------------------------
@mixin mq-ie() {
	@media screen and (-ms-high-contrast: none) {
		@content;
	}
}


/*	=============================================================================
	#fontsize
	========================================================================== */
// -----------------------------------------------------------------
// 文字サイズのrem変換とline-height、letter-spacingの一括指定
// $font-size：（必須）font-size の値（単位px）
// $line-height:：line-height の値（単位px）　なお 0 とすると line-height は出力しない。
// $letter-spacing：letter-spacing の値（photoshopのトラッキング値）　なお 0 とすると letter-spacing は出力しない。
// $is_center：letter-spacing を設定した状態で中央揃えした際の位置ずれ修正。
// -----------------------------------------------------------------
@mixin font-rem($font-size, $line-height:0, $letter-spacing:0, $is_center:false) {
	font-size: $font-size;
	font-size: $font-size / $_root-font-size-px * 1rem;

	@if($line-height !=0) {
		line-height: ($line-height / $font-size);
	}

	@if($letter-spacing !=0) {
		letter-spacing: ($letter-spacing / 1000) * 1em;

		@if($is_center==true) {
			text-indent: ($letter-spacing / 1000) * 1em;
		}
	}
}

// -----------------------------------------------------------------
// clearfixのスタイル挿入
// 引数$importantに"important"を渡すと'!important'を指定。
// -----------------------------------------------------------------
@mixin clearfix($important: false) {
	@if ($important=="important") {
		$important: unquote("!important");
	}

	@else {
		$important: null;
	}

	&:after {
		content: ""$important;
		display: block $important;
		clear: both $important;
	}
}

// -----------------------------------------------------------------
// テキストの両端揃え
// -----------------------------------------------------------------
@mixin justify() {
	text-align: justify;
	text-justify: inter-ideograph;
}

// -----------------------------------------------------------------
// サイズ不確定要素の上下左右中央配置
// 親要素がposition:relativeになっていることが前提
// 引数$axis: xy（上下左右方向）、x（横方向のみ）、y（縦方向のみ）
// 省略時は上下左右中央配置
// -----------------------------------------------------------------
@mixin centering($axis: xy) {
	position: absolute;

	@if ($axis==x) {
		left: 50%;
		transform: translateX(-50%);
	}

	@elseif ($axis==y) {
		top: 50%;
		transform: translateY(-50%);
	}

	@else {
		top: 50%;
		left: 50%;
		transform: translate(-50%, -50%);
	}
}

// -----------------------------------------------------------------
// display : flex;の指定
// -----------------------------------------------------------------

@mixin flex($flow:null, $justify:null, $align:null , $gutter:0) {
	display: flex;
	
	@if $flow == col {flex-flow: column wrap;}
	@elseif $flow == col-r {flex-flow: column-reverse wrap;}
	@elseif $flow == row-r {flex-flow: row-reverse wrap;}
	@else {flex-flow: row wrap;}
	
	@if $justify == center { justify-content: center; }
	@elseif $justify == end { justify-content: flex-end; }
	@elseif $justify == bet { justify-content:space-between; }
	@else { justify-content: flex-start; }
	
	@if $align == center {align-items: center;}
	@elseif $align == start {align-items:flex-start;}
	@elseif $align == end {align-items:flex-end;}
	@else{align-items:stretch;}
	
	margin-left: -#{$gutter}px;
	margin-right: -#{$gutter}px;
}

@mixin flexbox ($size, $gutter) {
	width: calc(((100% / 12) * #{$size}) - #{$gutter}px);
	flex-basis: calc(((100% / 12) * #{$size}) - #{$gutter}px);
	max-width: calc(((100% / 12) * #{$size}) - #{$gutter}px);
	margin-left : calc(#{$gutter}px / 2);
	margin-right: calc(#{$gutter}px / 2);
}
