.l-main{
	background: $_color-white;
	font-family: $_font-ja_serif;
	position: relative;
	@include mq-print(){
		background: $_color-white !important;
		color: $_color-black !important;
		& * {
			background: $_color-white !important;
			color: $_color-black !important;
		}
	}
}
html,body,main{
	@include mq-ie(){
		overflow-x: hidden;
		height: auto;
	}
}